import { BrowserRouter, Switch, Route } from "react-router-dom";
import { useEffect } from "react";
import { connect } from "react-redux";
import {getUser} from '../actions/'
import AdminLayout from './adminComponents/views/AdminLayout/Layout';
import Error404 from "./views/Error404";
// import FullScreenModal from './FullScreenModal';
import Layout from "./Layout";
import Home from "./views/Home";
import About from "./views/About";
import Login from "./views/Login";
import Signup from "./views/Signup";
import Contact from "./views/Contact";
import ProductPage from "./views/ProductPage";
import Category from "./views/Category";
import SearchResult from "./views/SearchResults";
import CartView from "./views/CartView";
import Order from "./views/Order";
import VerifyView from "./views/VerifyView";
import Loader from "./Loader";
import Dashboard from "./adminComponents/views/AdminDashboard/Dashboard";
import Products from "./adminComponents/views/AdminProducts/Products";
import ProductsDetail from "./adminComponents/views/AdminProductsDetail/ProductsDetail";
import AdminLogin from './adminComponents/views/AdminLogin/Login';
import UserManagement from './adminComponents/views/AdminUserManagement/UserManagement';
import UpdateProducts from './adminComponents/views/AdminUpdateProducts/UpdateProducts';
import ManageOrders from "./adminComponents/views/AdminManageOrders/ManageRequests";
import OrdersDetail from "./adminComponents/views/AdminOrdersDetail/OrdersDetail";

function App({getUser}) {
  useEffect(()=>{
    getUser()
  }, [getUser])
  return (
    <div className="App">
      <BrowserRouter>
        <Switch>
          <Route exact path="/" render={(props) =>(<Layout><Home {...props} /></Layout>)}/>

          <Route path="/signup" render={(props) =>(<Layout><Signup {...props} /></Layout>)}/>

          <Route path="/login" render={(props) =>(<Layout><Login {...props} /></Layout>)}/>

          <Route path="/about" render={(props) =>(<Layout><About {...props} /></Layout>)}/>

          <Route path="/contact" render={(props) =>(<Layout><Contact {...props} /></Layout>)}/>

          <Route path="/product/:product" render={(props) =>(<Layout><ProductPage {...props} /></Layout>)}/>

          <Route path="/category/:categoryName" render={(props) =>(<Layout><Category {...props} /></Layout>)}/>

          <Route path="/search/:query" render={(props) =>(<Layout><SearchResult {...props} /></Layout>)}/>

          <Route path="/cart" render={(props) =>(<Layout><CartView {...props} /></Layout>)}/>

          <Route path="/create-order" render={(props) =>(<Layout><Order {...props} /></Layout>)}/>

          <Route path="/auth/verify" render={(props) =>(<Layout><VerifyView {...props} /></Layout>)}/>

          {/*Admin Routes*/}

          <Route exact path="/admin" render={(props) =>(<AdminLayout title={'Dashboard'}><Dashboard {...props} /></AdminLayout>)}/>

          <Route path="/admin/user/manage" render={(props) =>(<AdminLayout title={'User Management'}><UserManagement {...props}/></AdminLayout>)}/>

          {/* <Route path="/admin/requests/" exact render={(props) =>(<AdminLayout title={'Manage Requests'}><ManageRequests {...props}/></AdminLayout>)} /> */}

          {/* <Route path="/admin/requests/:id" exact render={(props) =>(<AdminLayout title={'Manage Requests'}><RequestsDetail {...props}/></AdminLayout>)} /> */}

          <Route path="/admin/orders/" exact render={(props) =>(<AdminLayout title={'Manage Orders'}><ManageOrders {...props}/></AdminLayout>)}/>

          <Route path="/admin/orders/:id" exact render={(props) =>(<AdminLayout title={'Manage Orders'}><OrdersDetail {...props}/></AdminLayout>)}/>

          <Route path="/admin/products" exact render={(props) =>(<AdminLayout title={'Manage Products'}><Products {...props}/></AdminLayout>)}/>

          <Route path="/admin/create/products" exact render={(props) =>(<AdminLayout title={'Create Products'}><Products {...props}/></AdminLayout>)} />

          <Route path="/admin/edit/products/:id" render={(props) =>(<AdminLayout title={'Update Products'}><UpdateProducts {...props}/></AdminLayout>)}/>

          <Route path="/admin/products/:id" render={(props) =>(<AdminLayout title={'Manage Products'}><ProductsDetail {...props}/></AdminLayout>)}/>

          <Route path="/admin/login" render={(props) =>(<AdminLogin {...props}/>)}/>

          <Route render={(props) =>(<Layout><Error404 {...props}/></Layout>)}/>
        </Switch>
      </BrowserRouter>
      <Loader/>
    </div>
  );
}

export default connect(null, {getUser})(App);
