import {useState, useEffect} from 'react';
import api from '../../../../helpers/api'
import {Link} from 'react-router-dom'
import {FaArrowLeft, FaPen} from 'react-icons/fa'
import {capitalize, formatNumberWithCommas} from '../../../../helpers/util'
import Loader from 'components/Loader';

const ProductsDetail = ({match: {params: {id}}, history}) =>{
    const [data, setData] = useState([])
    const [loading, setLoading] = useState(true)
    useEffect(() => {
        api.get(`/products/${id}`)
            .then(res => {
                setData(res.data.product)
                setLoading(false)
            })
    }, [id])
    if(loading) return <Loader/>
    else return (
        <div className="ProductsDetail">
            <div className="content">
                <div className="back" onClick={history.goBack}>
                    <FaArrowLeft/>
                </div>
                <div className="top-row">
                    <div>
                        <div className="title">Product Name</div>
                        <div className="content">{data.title}</div>
                    </div>
                    
                    <div>
                        <div className="title">Location</div>
                        <div className="content">{data.location}</div>
                    </div>
                    <div>
                        <div className="title">Price</div>
                        <div className="content">{`₦ ${formatNumberWithCommas(data.price)}`}</div>
                    </div>
                    <div>
                        <div className="title">Category</div>
                        <div className="content">{`${capitalize(data.category)}`}</div>
                    </div>
                </div>
                <div className="bottom-row">
                    <div className="description">
                        <div className="title">Description</div>
                        <div className="content">{data.description}</div>
                    </div>
                    <div className="image">
                        <img src={data.imgUrl} alt="Product" />
                    </div>
                </div>
                <div className="controls">
                    <div className="content">
                        <button className="btn btn-red">Remove</button>
                        <Link className='btn btn-white' to={`/admin/edit/products/${id}`}>Edit <FaPen style={{marginLeft: '0.3rem'}}/></Link>
                    </div>
                </div>
            </div>
        </div>  
    )
}

export default (ProductsDetail)
