import {useEffect, useState} from "react";
import {FaArrowLeft, FaCheck} from 'react-icons/fa'
import api from "../../../../helpers/api";
import {formatNumberWithCommas} from "../../../../helpers/util";
import Loader from "../../../Loader";

const ProductsDetail = ({match: {params: {id}}, history}) =>{
    const [order, setOrder] = useState({})
    const [loading, setLoading] = useState(true)
    // const [totalPrice, setTotalPrice] = useState(0)
    useEffect(()=>{
        // if(loading){
            api.get(`/orders/${id}`)
                .then(res => {
                    const orderRes = res.data.order
                    setOrder(orderRes)
                    setLoading(false)
                })
        // }

    }, [id])
    console.log(order)
    if (loading) {
        return <Loader/>
    }
    else return (
        <div className="OrdersDetail">
            <div className="content">
                <div className="back" onClick={history.goBack}>
                    <FaArrowLeft/>
                </div>
                <div className="top-row">
                    <div>
                        <div className="title">Name</div>
                        <div className="content">{order.name}</div>
                    </div>
                    <div>
                        <div className="title">Email</div>
                        <div className="content">{order.email}</div>
                    </div>
                    <div>
                        <div className="title">Phone Number</div>
                        <div className="content">{order.phone}</div>
                    </div>
                    <div>
                        <div className="title">status</div>
                        <div className="content text-yellow">Pending</div>
                    </div>
                </div>

                <div className="top-row">
                    <div>
                        <div className="title">Addresss</div>
                        <div className="content">{order.address}</div>
                    </div>
                    <div>
                        <div className="title">Order ID</div>
                        <div className="content">#{order.id}</div>
                    </div>
                    <div>
                        <div className="title">Delivery Method</div>
                        <div className="content">{order.deliveryMethod==='homeDelivery'?'Home Delivery':'Pick Up'}</div>
                    </div>
                    <div>
                        <div className="title">Order Time</div>
                        <div className="content">{new Date(order.time).toLocaleString()}</div>
                    </div>
                    <div>
                        <div className="title">State of Residence</div>
                        <div className="content">Lagos</div>
                    </div>
                </div>

                <div className="bottom-row">
                    <div className="itemList">
                        <div className="title">Items</div>
                        <div className="content">
                        {order.items.map(item =>(
                        <div className="item" key={item._id} >
                            <img src={item.product.imgUrl} alt="Product" />
                            <h4>{item.product.title}</h4>
                            <div className="details">
                                <div>
                                    <h4>Quantity</h4>
                                    <p>{item.quantity}</p>
                                </div>
                                <div>
                                    <h4>Price</h4>
                                    <p>₦{formatNumberWithCommas(item.product.price)}</p>
                                </div>
                            </div>
                        </div>
                        ))}
                        <br />
                    <h3>Total price: ₦{formatNumberWithCommas(order.totalPrice)}</h3>

                        </div>
                    </div>
                </div>
                    
                <div className="controls">
                    <div className="content">
                        <button className="btn btn-primary"><span style={{marginRight: '0.5rem'}}><FaCheck/></span> Mark as Delivered</button>
                    </div>
                </div>
            </div>
        </div>  
    )
}

export default ProductsDetail
