import {formatNumberWithCommas} from '../helpers/util'
import {withRouter} from 'react-router-dom'

const ProductCard = ({product, history}) =>{
    const onItemClick = (e)=>{
        history.push(`/product/${product._id}`)
    }
    return(
        <div className="ProductCard" onClick={onItemClick}>
            <div className="img-container">
                <img src={product.imgUrl} alt={product.title}/>
            </div>

            <div className='info'>
                <p className="text">{product.title}</p>
                <p className="price text-primary">₦{formatNumberWithCommas(product.price)}</p>
            </div>
        </div>
    )
}
export default withRouter(ProductCard)