import Loader from 'components/Loader'
import {useState, useEffect} from 'react'
import api from "../../helpers/api"
import Footer from "../Footer"
import Header from "../Header"
import ProductList from "../ProductList"

const SearchResult = (props) => {
    const searchQuery = props.match.params.query.split(/[\s+]+/).join(' ')
    const [loading, setLoading] = useState(true)
    const [result, setResult] = useState([])
    useEffect(() => {
        api.post('/products/search', {search: searchQuery})
        .then(res => {
            setResult(res.data.products)
            setLoading(false)
        })
        .catch(e =>{
            console.log(e)
            // TODO: Display Error modal and stop loading
        })
    }, [setResult, setLoading, searchQuery]);


    // const fuse = new Fuse(products, {
    //     threshold: 0.45,
    //     minMatchCharLength: 3,
    //     includeScore: true,
    //     keys: [
    //         "title","category"
    //     ]
    // });

    
    // const result = fuse.search(searchQuery).map(item =>products[item.refIndex])
    if (loading) return <Loader/>
    else return(
        <div className="SearchResult">
            <Header/>
            <div className="container content">
                <h2>Results for: "{searchQuery}"</h2>
                <small>{result.length} {result.length === 1?'result':'results'} found</small>
                <ProductList products={result}/>
            </div>
            <Footer/>
        </div>
    )
}
export default SearchResult