import api from '../helpers/api'
import types from './types'

export const loginUser  = (email, password) => async (dispatch) =>{
    try{
        const res = await api.post('/auth/login', { email, password})
        const token = res.data.token
        localStorage.setItem("token", JSON.stringify(token))
        api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        dispatch({
            type: types.SIGN_IN,
            payload: {
                isAuthenticated: true,
                email: res.data.email,
                name: res.data.name,
                error: "",
                token
            }
        })
    }
    catch(e){
        if (!e.response) return dispatch({
            type: types.AUTH_ERROR,
            payload: "Check your network connection"
        })

        dispatch({
            type: types.AUTH_ERROR,
            payload: e.response.data.message
        })
    }
}

export const signupUser  = (name, email, password, phone) => async (dispatch) =>{
    try{
        await api.post('/auth/register', { email, name, password, phone})
        dispatch({
            type: types.SIGN_UP,
            payload: {
                isAuthenticated: false,
                email: email, 
                error: "",
            }
        })
        return true
    }
    
    catch(e){
        if (!e.response) return dispatch({
            type: types.AUTH_ERROR,
            payload: "Check your network connection"
        })

        dispatch({
            type: types.AUTH_ERROR,
            payload: e.response.data.message
        })
        return false
    }
}

export const getUser = () => async dispatch => {
    const token = JSON.parse(localStorage.getItem('token'))
    if(token){
        try{
            api.defaults.headers.common['Authorization'] = `Bearer ${token}`
            const res = await api.get('/auth/user')
            dispatch({
                type: types.GET_USER,
                payload: {isAuthenticated: true, ...res.data}
            })
        }

        catch(e){
            if(e.response.status === 401) localStorage.removeItem('token')
        }
    }
}

export const logout = () => async dispatch => {
    localStorage.removeItem('token')
    dispatch({
        type: types.LOGOUT,
        payload: {isAuthenticated: false}
    })
}

export const fetchProducts = () => dispatch =>{
    dispatch({
        type: types.FETCHING_PRODUCT
    })
    api.get('/products')
    .then(res => dispatch({
            type: types.FETCH_PRODUCTS,
            payload: [...res.data.products]
        })
    )
    .catch(({status, data, statusText}) => {
        dispatch({
            type: types.FETCH_ERROR,
            payload: {status, statusText, data}
        })
    })
}

export const fetchProduct = (id) => async dispatch =>{
    try{
        const res =  await api.get(`/products/${id}`)
        dispatch({
            type: types.FETCH_PRODUCTS,
            payload: [res.data.product]
        })
    }
    catch (e) {
        return false
    }
}

export const fetchProductCategory = (category) => async dispatch =>{
    dispatch({
        type: types.FETCHING_PRODUCT
    })
    try{
        const res =  await api.get(`/products?category=${category}`)
        dispatch({
            type: types.FETCH_PRODUCTS,
            payload: [...res.data.products]
        })
        return res.data.products
    }
    catch (e) {
        return false
    }
}

export const createOrder = ({name, phone, address, state, items, email, deliveryMethod, totalPrice}) => async dispatch =>{
    try{
        await api.post(`/orders`, {
            name, phone, address, state, email, items, deliveryMethod, totalPrice
        })
        // dispatch({
        //     type: types.CREATE_ORDER,
        //     payload:
        // })
        return true
    }
    catch (e) {
        return false
    }
}