import {useEffect, useState} from 'react'
import { fetchProductCategory } from 'actions'
import {connect} from 'react-redux'
import api from '../../helpers/api'
import Footer from "../Footer"
import Header from "../Header"
import ProductList from '../ProductList'
import Error404 from './Error404'
import Loader from 'components/Loader'

const Category = ( {isLoading, fetchProductCategory, products, ...props}) => {
    const category = props.match.params['categoryName'].split('-').join(' ')
    const [categories, setCategories] = useState([])
    useEffect(()=>{
        fetchProductCategory(category)
        api.get('/category')
            .then(res => setCategories(res.data.categories))
    }, [fetchProductCategory, category])
    const categoryName = category.split(' ').map(str=> str.charAt(0).toUpperCase() + str.slice(1)).join(' ')
    const filteredProducts = products.filter(product => product.category.includes(category))
    const categoryList = categories.map(category => category.value)
    if(categories.length<1 ) return <Loader/>
    else if (isLoading) return <Loader/>
    else if (!categoryList.includes(category)){
        return(
            <><Error404/></>
        )
    }
    else return(
        <div className="Category">
            <Header/>
            <div className="container content">
                <small>{`Categories > ${categoryName}`}</small>
                <h2 className='title'>{categoryName}</h2>
                <ProductList products={filteredProducts}/>
            </div>
            <Footer/>
        </div>
    )
}

const mapStateToProps = ({products}) => ({
    products: products.products,
    isLoading: products.isLoading,
    error: products.error,
})
export default connect(mapStateToProps, {fetchProductCategory})(Category)