import{useState, useEffect} from 'react' 
import api from '../../../helpers/api'
import MUIDataTable from "mui-datatables";
import { MuiThemeProvider,} from "@material-ui/core/styles";
import {getMuiTheme} from '../../../config'

const columns = [
    {label: 'Name', name: 'name', options: {filter: true, sort: true,}},
    // {label: 'Last Name', name: 'lastName', options: {filter: false, sort: true,}},
    {label: 'Email', name: 'email', options: {filter: false, sort: false,}},
    {label: 'Phone Number', name: 'phone', options: {filter: false, sort: false,}},
    {label: 'Date Created', name: 'dateCreated', options: {
        filter: false, 
        sort: false,
        customBodyRender: dateCreated => <p>{new Date(dateCreated).toLocaleDateString()}</p>
    }},
    {label: 'Status',name: 'isVerified', options: {
        filter: true, 
        sort: true,
        customBodyRender: isVerified => <span className={isVerified? 'text-green' : 'text-yellow'}>{isVerified?'Active':'Inactive'}</span>
    } },
]
// const data = Array(125).fill().map((x,index) => ({
//     id: index,
//     firstName: faker.name.firstName(),
//     lastName: faker.name.lastName(),
//     email: faker.internet.email(),
//     phoneNumber: faker.phone.phoneNumber(),
//     status: Math.floor(Math.random()*2)?'Active': 'Inactive'
// }))

const options = {
    filterType: 'checkbox',
    download: false,
    rowsPerPage: 6,
    rowsPerPageOptions: [5, 6, 10, 100],
    searchPlaceholder: 'Search Here...',
    print: false,
    viewColumns: false

};

const TableComponent = ({setTotalUsers}) => {
    const [data, setData] = useState([])
    useEffect(() => {
        api.get('/auth/users')
            .then(res => {
                setData(res.data.users);
                setTotalUsers(res.data.users.length);
            })
    }, [setTotalUsers])
    console.log(data)
    return(
        <MuiThemeProvider theme={getMuiTheme()}>
        <MUIDataTable
            // title={"Employee List"}
            data={data}
            columns={columns}
            options={options}
            />
        </MuiThemeProvider>
    )
}

export default TableComponent
