import {useState, useEffect} from 'react'
import api from '../../../helpers/api'
import MUIDataTable from "mui-datatables";
import DetailLink from '../AdminDetailLink/DetailLink';
import { MuiThemeProvider,} from "@material-ui/core/styles";
import {getMuiTheme} from '../../../config'
import { capitalize, formatNumberWithCommas} from '../../../helpers/util'

const columns = [
    {label: 'Category', name: 'category', 
    options: {
        filter: true,
        sort: true,
        customBodyRender: category => capitalize(category),
    }},
    {label: 'Product Name', name: 'title', options: {filter: false, sort: true,}},
    {label: 'Price', name: 'price', 
    options: {
        filter: false, 
        sort: true,
        customBodyRender: price => `₦${formatNumberWithCommas(price)}`,
    }},
    {label: 'Location', name: 'location', options: {filter: true, sort: false,}},
    {label: 'Stock',name: 'stock', options: {
        filter: true, 
        sort: true,
        customBodyRender: stock => <span className={stock < 5 ? 'text-danger' : 'text-green'}>{stock}</span>
    } },
    {label: ' ',name: '_id', options: {
        filter: false, 
        sort: false,
        customBodyRender: _id => <DetailLink path={`/admin/products/${_id}`}/>,
    } },
]

const options = {
    filterType: 'checkbox',
    download: false,
    rowsPerPage: 6,
    rowsPerPageOptions: [5, 6, 7, 8, 9, 10, 100],
    searchPlaceholder: 'Search Here...',
    // customSearchRender: ()=>'search',
    print: false,
    viewColumns: false
};

const TableComponent = () => {
    const [data, setData] = useState([])
    useEffect(() => {
        api.get('/products')
            .then(res => setData(res.data.products))
    }, [])
    return(
        <MuiThemeProvider theme={getMuiTheme()}>
            <MUIDataTable
                // title={"Employee List"}
                data={data}
                columns={columns}
                options={options}
                />
        </MuiThemeProvider>
    )
}

export default TableComponent
